import React from 'react';
// import TodoItem from "./TodoItem/TodoItem";
import TodoItem from './TodoItem/TodoitemClass'
import Header from "./Header";
// import TodoInput from './TodoInput';
import TodoInput from './TodoInputFunc'
import '../App.scss';
import axios from 'axios'
import { DragDropContext, Droppable } from 'react-beautiful-dnd';


// class component
class TodoContainer extends React.Component {

    state = {
        todos: [{
            title: 'test',
            completd: false,
            _id: 2
        }],
        isLoading: false,
        timeGetBusy: false,
    }

    componentDidMount() {
        this.setState({ isLoading: true })
        axios.get('https://majd-todo.herokuapp.com/todo')
            .then(
                response => {
                    this.setState({
                        todos: response.data,
                        isLoading: false
                    })
                }).catch(err => {
                    console.log(err)
                });
    }

    componentDidUpdate() {
        if (this.state.todos.length >= 10 && this.state.timeGetBusy === false) {
            this.setState({ timeGetBusy: true })
        } else if (this.state.todos.length < 10 && this.state.timeGetBusy === true) {
            this.setState({ timeGetBusy: false })
        }
    }

    componentWillUnmount () {
        console.log('deleted')
    }

    onChangeCheckBox = (id) => {
        axios.put(`https://majd-todo.herokuapp.com/todo/${id}`)
        const updateTodo = this.state.todos.map(todo => {
            if (todo._id === id) {
                todo.completed = !todo.completed
            }
            return todo
        })
        this.setState({
            todos: updateTodo
        })
    }

    onDelete = (id) => {
        axios.delete(`https://majd-todo.herokuapp.com/todo/${id}`)
            .then(() => {
                const deleteTodo = this.state.todos.filter(todo => todo._id !== id)
                this.setState({
                    todos: deleteTodo
                })
            }).catch(err => {
                console.log(err);
            });
    }

    handleAddTodo = (todo) => {
        this.setState({ isLoading: true })
        let title = todo;
        axios.post('https://majd-todo.herokuapp.com/todo', { title })
            .then(res => {
                this.setState({
                    todos: [...this.state.todos, res.data],
                    isLoading: false
                })
            }).catch(err => {
                console.log(err);
            });
    }

    onDragEnd = (result) => {
        // combining item
        const { destination, source, reason } = result
        if (!destination || reason === 'CANCEL') {
            return
        }
        if (destination.droppableId === source.droppableId && destination.index === source.index) {
            return;
        }
        const todos = Object.assign([], this.state.todos);
        const droppedTodo = this.state.todos[source.index];
        todos.splice(source.index, 1);
        todos.splice(destination.index, 0, droppedTodo);

        this.setState({ todos })
    }


    render() {
        let completd = this.state.todos.filter((todo) => todo.completed);
        return (
            <div className="container" style={this.state.isLoading ? { opacity: "0.3" } : null}>

                <Header timeGetBusy={this.state.timeGetBusy} />
                <TodoInput addTodo={this.handleAddTodo} timeGetBusy={this.state.timeGetBusy} />
                {this.state.isLoading ? <p>Bitte Warten</p> : null}
                <span style={{ color: 'green', fontSize: 12 }}>Todo completd: {completd.length}</span>
                <DragDropContext onDragEnd={this.onDragEnd}>
                    <ul style={{ padding: 40 }}>
                        <Droppable droppableId='dp1'>
                            {(porvided => (<div ref={porvided.innerRef} {...porvided.droppableProps} >
                                {this.state.todos.map((todo, index) => (
                                    <TodoItem
                                        key={todo._id}
                                        title={todo.title}
                                        id={todo._id}
                                        index={index}
                                        completed={todo.completed}
                                        handleChange={this.onChangeCheckBox}
                                        handleDelete={this.onDelete} />

                                ))}
                                {porvided.placeholder}
                            </div>))}

                        </Droppable>
                    </ul>
                </DragDropContext>
            </div>
        );
    }
}





export default TodoContainer;