import React, { useState } from 'react';

function TodoInputFunc(props) {
    const [title, setTitle] = useState('');
    const [error, setError] = useState(null);

    const onChangeHandler = (e) => {
        setTitle(e.target.value)
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        if (title === '') {
            return setError("must not to be empty" );
        }else if ( title.length >= 25){
            return setError("must not to be more 25 Character" );
        }else if (title.includes('@')) {
            return setError("Please don't use @ in your Todos" );
        }else {
            setError(null)
            props.addTodo(title)
            setTitle("")
        }
    }
    return (
        <div>
            {error && <p style={{ color: "red" ,padding:"20px 0"}}>{error}</p>}
            <form
                onSubmit={handleSubmit}
                className='form'>
                <input
                    type="text"
                    name="title"
                    placeholder="Dein ToDo muss zwischen 3 und 25 Zeichen lang sein!"
                    onChange={onChangeHandler}
                    value={title}
                    className='input-text'
                    minLength='3'
                    style={{
                        outline : error && '1px solid red',
                        color:title.length < 3 || title.length > 25 ? 'red' : 'green'}}
                   
                    disabled={props.timeGetBusy} />
                <button
                    type="submit"
                    className='input-submit'
                    disabled={props.timeGetBusy}
                    style={props.timeGetBusy ? { backgroundColor: '#eee' } : null}>
                    hinzuf√ºgen
                </button>
            </form>
        </div>
    )
}

export default TodoInputFunc
