import React from 'react';
import todoImg from '../images/3227392.jpg'

const Header = (props) => {
    const styles = {
        h1: {
            textAlign: "center",
            color: "gray",
            marginBottom: props.timeGetBusy ? 10: 20
        }
    }
    return (
        <div >
            <img  src={todoImg} alt='todoIcon' style={{ width:250,  margin:'auto', display:'block'}}/>
            <h1 style={styles.h1 }>To Do App</h1>
            {props.timeGetBusy &&
                <h2 style={{ ...styles.h1, marginBottom: '30px' }}>
                    Time to get busy!
            </h2>
            }
            <p style={{ padding: '20px 0' }}>Add, Edit or Delete a Todo Item.</p>
        </div>
    )
}

export default Header;