import React, { useState, useEffect } from 'react'

function TodoDetail(props) {
    const styleTodo = {
        padding: 20,
    }
    const [todo, setTodo] = useState({ title: props.location.detailProps })
    if (!todo.title) return <div style={styleTodo}><h3>keine todo zur Zeigen </h3></div>
    return (
        <div style={styleTodo}>
            <h3>{props.location.detailProps}</h3>
            <small>du hast den todo mit id : {props.match.params.id}</small>
        </div>
    )
}

export default TodoDetail
